'use client';

// (Este é o mesmo card de metas da sua Dashboard)
function GoalsCard() {
  const metaCasamento = {
    nome: "Meta: Casamento",
    progresso: 15,
    atual: 3000,
    total: 20000
  };

  return (
    <div className="bg-gray-800 p-6 rounded-lg shadow-lg h-full">
      <h2 className="text-2xl font-bold mb-4 text-yellow-400">Suas Metas</h2>
      <div>
        <div className="flex justify-between items-center mb-1">
          <span className="font-semibold text-lg">{metaCasamento.nome}</span>
          <span className="text-sm font-medium text-yellow-400">{metaCasamento.progresso}%</span>
        </div>
        <div className="w-full bg-gray-700 rounded-full h-2.5 mb-2">
          <div 
            className="bg-yellow-500 h-2.5 rounded-full" 
            style={{ width: `${metaCasamento.progresso}%` }}
          ></div>
        </div>
        <div className="flex justify-between items-center text-sm text-gray-300">
          <span>R$ {metaCasamento.atual.toLocaleString('pt-BR')}</span>
          <span>R$ {metaCasamento.total.toLocaleString('pt-BR')}</span>
        </div>
      </div>
      {/* Aqui podemos adicionar um formulário para criar novas metas no futuro */}
    </div>
  );
}


export default function MetasPage() {
  return (
    <div>
      <h1 className="text-3xl font-bold mb-6">Página de Metas</h1>
      <p className="mb-8 text-gray-300">Aqui você poderá criar, editar e acompanhar o progresso de todas as suas metas financeiras.</p>
      
      {/* Reutiliza o card do dashboard */}
      <div className="max-w-md">
        <GoalsCard />
      </div>
    </div>
  );
}